@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
.container{display:flex;flex-direction:column;align-items:center;justify-content:center;text-align:start;max-width:570px;height:500px;background:#fff;box-shadow:5px 10px 30px rgba(0,0,0,0.05);border-radius:16px;margin:0 auto;padding:29px 35px 35px}.form__label{font-weight:700;margin-top:30px;margin-bottom:12px}.form__inputs{position:relative;display:flex;flex-direction:column;max-width:500px}.form__input{width:500px;height:70px;outline:none;border-radius:16px;padding:24px 25px;border:1px solid #C1C1C1}.form__input:focus{border:1px solid #3C9C41}.form__input-norm{border:1px solid #C1C1C1}.form__input-err{border:1px solid #D23737;color:#D23737}.form__input-error{color:#D23737}.form__input-icon{position:absolute;width:30px;height:30px;top:85px;right:24px;z-index:100;cursor:pointer;background:inherit;border:none}.form__input-btn{width:500px;height:70px;font-size:16px;font-weight:700;color:#fff;background-color:#3C9C41;border:none;border-radius:16px;margin-top:20px;margin-bottom:16px;cursor:pointer;transition:background-color 0.3s}.form__input-btn:hover{background-color:#4DB252}.form__input-btn:active{background-color:#318636}@media (max-width: 550px){.form__input{width:308px;height:50px}.form__input-btn{width:308px;height:50px}.form__input-icon{top:75px;right:17px}}@media (max-width: 350px){.form__input{width:270px;height:45px}.form__input-btn{width:270px;height:45px}}

.modal{position:relative;height:100vh;width:100vw;background-color:rgba(0,0,0,0.4);position:fixed;top:0;left:0;display:flex;justify-content:center;z-index:200;opacity:0;pointer-events:none;transition:0.5s}.modal--active{opacity:1;pointer-events:all}.modal__content{width:570px;height:500px;margin:15px;padding:15px;border-radius:12px;background-color:#fff;display:flex;flex-direction:column;align-items:center;justify-content:center;border:2px solid #C1C1C1;-webkit-transform:scale(0.5);transform:scale(0.5);transition:0.4s all}.modal__content--active{-webkit-transform:scale(1);transform:scale(1)}.modal__button{width:300px;height:60px;font-size:16px;font-weight:700;color:#fff;background-color:#3C9C41;border-radius:16px;border:none;margin-top:20px;margin-bottom:16px;cursor:pointer;transition:background-color 0.3s}.modal__button:hover{background-color:#4DB252}.modal__button:active{background-color:#318636}.modal__button--close{position:absolute;right:10px;bottom:0}.form__input{margin-bottom:15px}

.container{position:relative;display:flex;flex-direction:column;align-items:center;justify-content:center;text-align:start;max-width:570px;height:500px;background:#fff;box-shadow:5px 10px 30px rgba(0,0,0,0.05);border-radius:16px;margin:0 auto;padding:29px 35px 35px}.btn{position:absolute;top:15px;left:15px;width:200px;height:50px;font-size:16px;font-weight:700;color:#fff;background-color:#3C9C41;border:none;border-radius:16px;margin-top:20px;margin-bottom:16px;cursor:pointer;transition:background-color 0.3s}.btn:hover{background-color:#4DB252}.btn:active{background-color:#318636}

.user{position:relative;height:100vh;width:100vw;background-color:rgba(0,0,0,0.4);position:fixed;top:0;left:0;display:flex;justify-content:center;z-index:200;opacity:0;pointer-events:none;transition:0.5s}.user--active{opacity:1;pointer-events:all}.user__content{width:570px;height:500px;margin:15px;padding:15px;border-radius:12px;background-color:#fff;display:flex;flex-direction:column;align-items:center;justify-content:center;border:2px solid #C1C1C1;-webkit-transform:scale(0.5);transform:scale(0.5);transition:0.4s all}.user__content--active{-webkit-transform:scale(1);transform:scale(1)}.user__button{width:300px;height:60px;font-size:16px;font-weight:700;color:#fff;background-color:#3C9C41;border-radius:16px;border:none;margin-top:20px;margin-bottom:16px;cursor:pointer;transition:background-color 0.3s}.user__button:hover{background-color:#4DB252}.user__button:active{background-color:#318636}.user__button--close{position:absolute;right:10px;bottom:0}

.icon-style{width:40px;height:40px}.chat{display:flex;flex-direction:column;min-width:570px;height:500px;background:#fff;box-shadow:5px 10px 30px rgba(0,0,0,0.05);border-radius:16px;margin:0 auto;padding:10px 20px}.chat__input{display:flex;flex-direction:column;min-height:65%;padding:15px;border:2px solid blue}.chat__header,.chat__form{display:flex;flex-direction:row;align-items:center;justify-content:space-between}.chat__input--text{width:100%;height:50px;outline:none;border-radius:16px;padding:24px 25px;border:1px solid #C1C1C1;margin-top:20px;margin-bottom:16px}.chat__input--text:focus{border:1px solid #3C9C41}.chat__btn{display:flex;text-align:center;align-items:center;justify-content:center;width:150px;height:50px;font-size:16px;font-weight:700;color:#fff;text-decoration:none;background-color:#3C9C41;border:none;border-radius:10px;margin-top:20px;margin-bottom:16px;cursor:pointer;transition:background-color 0.3s}.chat__btn:hover{background-color:#4DB252}.chat__btn:active{background-color:#318636}

*{box-sizing:border-box}body{font-family:'Montserrat', sans-serif;color:#5B5B5B;font-size:16px;background:#E5E5E5}

